<template>
  <div>
    <form @submit.prevent="insetarSocio">
            <div>
                <input
                    type='number'
                    name='cedula'
                    class='form-control'
                    placeholder='Cedula'
                    autoComplete='off'
                    v-model="Addsocio.cedula"
                />
                <input
                    type='text'
                    name='nombre'
                    class='form-control mt-3'
                    placeholder='Nombre'
                    autoComplete='off'
                    v-model="Addsocio.nombre"
                />
                <input
                    type='text'
                    name='apellidos'
                    class='form-control mt-3'
                    placeholder='Apellidos'
                    autoComplete='off'
                    v-model="Addsocio.apellidos"

                />
                 <input
                    type='number'
                    name='celular'
                    class='form-control mt-3'
                    placeholder='Celular'
                    autoComplete='off'
                    v-model="Addsocio.celular"

                />
                <input
                    type='text'
                    name='placa'
                    class='form-control mt-3'
                    placeholder='Placa'
                    autoComplete='off'
                    v-model="Addsocio.placa"
       
                />

                <button 
                class='btn btn-primary form-control  mt-3'
                type='submit'  
                :disabled="getIsLoading"
                >
                Ingresar

                </button>
            </div>
        </form>
  </div>
</template>

<script>
    
    import { fetchSinToken } from '@/help/fetch'
import Swal from 'sweetalert2'
import { mapActions, mapState, mapGetters } from 'vuex';
   
export default {
    name:'FormSocio',
    data(){
        return{
            Addsocio: {},
           
        }
    },
    computed:{
        ...mapState('socio',['socio']),
        ...mapGetters('socio',['getIsLoading'])
    },
    methods:{
        ...mapActions('socio', ['addSocio', 'getSocios']),
        async insetarSocio (){
            var socio = {
                cedula: this.Addsocio.cedula,
                nombre: this.Addsocio.nombre,
                apellidos: this.Addsocio.apellidos,
                celular: this.Addsocio.celular,
                placa: this.Addsocio.placa
            }
            await this.addSocio(socio)
            
        }
    
    }
}
</script>

